.App {
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif ;
  font-size: 60px;
  text-align: center;
  margin-top: 0%;
  color: #fff;
  text-shadow: 1px 1px rgb(0, 0, 0);
  left: 50%;
}

.App-title {
  font-size: 20px;
}

.Clock-days, .Clock-hours, .Clock-minutes, .Clock-seconds, .Days {
  display: inline;
  margin: 1%;
  }

.time-days, .time-hours, .time-minutes, .time-seconds  {

  display: inline;
  margin: 2%;
  font-size: 15px;
  }

.center {
  text-align: center;
  display: inline;
  z-index: 1;
}
body, #slider, .wrap, .slide-content{
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  width: 100%;
  height: 100vh;
  overflow-x: hidden;
}

.wrap{position: relative}

.slide{
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}
.slide1{background-image: url(/uk-france2019/static/media/image1.10d9b4b5.jpg)}
.slide2{background-image: url(/uk-france2019/static/media/image2.cf04da4b.jpg)}
.slide3{background-image: url(/uk-france2019/static/media/image3.99792ff3.jpg)}

.slide-content{
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.slide-content span{
  font-size:5rem;
  color: #fff;
}
.arrow{
  cursor: pointer;
  position: absolute;
  top: 50%;
  margin-top: -50px;
  width: 0;
  height: 0;
  border-style: solid;
}

#arrow-left{
  border-width: 30px 40px 30px 0;
  border-color: transparent #fff transparent transparent;
  left: 0;
  margin-left:30px;
}
#arrow-right{
  border-width: 30px 0 30px 40px;
  border-color: transparent transparent transparent #fff ;
  right: 0;
  margin-right:30px;
}
